.pokemonList {
    margin-left: 30px;
}

.pokemonTitle {
    font-size: 30px;
    font-weight: 900;
    padding: 10px;
}

.pokemonListBtn {
    box-shadow: 0px 10px 14px -7px #276873;
    background: linear-gradient(to bottom, #599bb3 5%, #408c99 100%);
    background-color:#599bb3;
    border-radius: 8px;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 16px;
    font-weight: bold;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-left: auto;
    margin-right: 5px;
    padding: 5px 10px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #3d768a;
}

.pokemonListBtn:hover {
    background: linear-gradient(to bottom, #408c99 5%, #599bb3 100%);
    background-color: #408c99;
}

.pokemonListBtn:active {
    position: relative;
    top: 1px;
}